import api from '@/lib/api';

export const getCategories = () => api.get("/v1/categories");
export const createCategory = (data: any) => api.post("/v1/categories", data);
export const updateCategory = (id: number, data: any) => api.put(`/v1/categories/${id}`, data);
export const deleteCategory = (id: number) => api.delete(`/v1/categories/${id}`);
